import React, { Component } from 'react';
import StyledCard from '../styles/StyledCard';
import FormInput from './FormInput';

class Goal extends Component {

    state = {
        goal: ''
    }

    handleChange = (e) => {
        const { name, type, value } = e.target;
        const val = type === 'number' ? parseFloat(value) : value;
        this.setState({
            [name]: val
        })
    }

    render() {
        return (
            <div>
                <StyledCard>
                    <FormInput name="goal" niceName="Goal" onChange={this.handleChange} value={this.state.goal} placeholder="Add in your goal" />

                </StyledCard>



            </div>
        );
    }
}

export default Goal;